.page {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: $lineHeight;
  color: $textColor;
  background-color: $bgColor;

  overflow-x: hidden;

  scroll-behavior: smooth;
  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
      height: 100vh;
    }
  }
}
